define('sudoku/constraints/result', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Object.extend({

    valid: false,
    invalidCells: null,

    init: function init() {
      if (null === this.get("invalidCells")) {
        this.set("invalidCells", []);
      }
    }

  });

});